.signUpScreen {
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0, 0, 0, 0.85);
}

.signUpScreen>form {
    display: flex;
    flex-direction: column;
}

.signUpScreen>form>h1{
    text-align: center;
    margin-bottom: 25px;
}
.signUpScreen>form>h4{
    text-align: left;
    margin-top: 30px;
}
.signUpScreen__grey{
    color: grey;

}
.signUpScreen__link{
    cursor: pointer;
    text-decoration: underline;
}

.signUpScreen>form>input {
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
}

.signUpScreen>form>button {
    padding: 16px 20px;
    font-size: 1rem;
    color: wheat;
    border-radius: 5px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;

}