.loginScreen {
    position: relative;
    height: 100%;
    background: url("https://pinkroom-main.s3.amazonaws.com/f75774ea-9a7c-44ea-a7c7-91d1e9df10e6.jpg") center no-repeat;
    background-size: cover;

}

.loginScreen__gradient {
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(to top,
            rgba(0, 0, 0, 0.8)0,
            rgba(0, 0, 0, 0)60%,
            rgba(0, 0, 0, 0.8)100%);
}

.loginScreen__logo {
    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.loginScreen__button {
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color: white;
    background-color: #e50914;
    cursor: pointer;
    border: none;
    font-weight: 600;
}

.loginScreen__body {
    z-index: 1;
    color: white;
    padding: 20px;
    position: absolute;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    left: 0;
    right: 0;
}

.loginScreen__body>h2 {
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}
.loginScreen__body>h3{
    font-size: 1.3rem;
    font-weight: 400;
}

.loginScreen__body>h1 {
    font-size: 3.125rem;
    margin-bottom: 20px;
}
.loginScreen__Input{
    margin: 20px;
}
.loginScreen__Input>form>input{
    outline-width: 0;
    padding: 10px;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}
.loginScreen__getStarted{
    padding: 16px 20px;
    font-size: 1rem;
border: none;
    color: white;
    background-color: #e50914;
    font-weight: 600;
    cursor: pointer;
}